<template>
  <v-container fluid>
    <v-layout row wrap>

      <!-- dashboard side nav -->
      <v-flex xs12 sm3 lg2>
        <v-card 
        class="my-4"
        height="600px">
          <v-navigation-drawer
            v-model="drawer"
            permanent
            absolute
          >
            <v-toolbar flat class="transparent">
              <v-list class="pa-0">
                <v-list-tile avatar>
                  <v-list-tile-avatar>
                    <img :src="vendor.avatar">
                  </v-list-tile-avatar>

                  <v-list-tile-content>
                    <v-list-tile-title>{{vendor.name}}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
            </v-toolbar>

            <v-list class="pt-0" dense>
              <v-divider></v-divider>

              <v-list-tile
              @click="">
                <v-list-tile-action>
                  <v-icon>insert_chart</v-icon> 
                </v-list-tile-action>
                <v-list-tile-title>Dashboard</v-list-tile-title>
              </v-list-tile>

              <v-list-group
                prepend-icon="account_circle"
                value="true"
              >
                <v-list-tile slot="activator">
                  <v-list-tile-title>Manage Account</v-list-tile-title>
                </v-list-tile>

                <v-list-group
                  no-action
                  sub-group
                  value="true"
                >
                  <v-list-tile slot="activator">
                    <v-list-tile-title>Admin</v-list-tile-title>
                  </v-list-tile>

                  <v-list-tile
                    v-for="(admin, i) in admins"
                    :key="i"
                    @click=""
                  >
                    <v-list-tile-title v-text="admin[0]"></v-list-tile-title>
                    <v-list-tile-action>
                      <v-icon v-text="admin[1]"></v-icon>
                    </v-list-tile-action>
                  </v-list-tile>
                </v-list-group>

                <v-list-group
                  sub-group
                  no-action
                >
                  <v-list-tile slot="activator">
                    <v-list-tile-title>Actions</v-list-tile-title>
                  </v-list-tile>

                  <v-list-tile
                    v-for="(crud, i) in cruds"
                    :key="i"
                    @click=""
                  >
                    <v-list-tile-title v-text="crud[0]"></v-list-tile-title>
                    <v-list-tile-action>
                      <v-icon v-text="crud[1]"></v-icon>
                    </v-list-tile-action>
                  </v-list-tile>
                </v-list-group>
              </v-list-group>
            </v-list>
          </v-navigation-drawer>
        </v-card>
      </v-flex>

      <!-- Dashboard items -->
      <v-flex xs12 sm9 lg10
      class="dashboard">
        <v-card class="mt-4">

        <!-- Performance Chart -->
        <v-card-title primary-title>
          <v-layout row wrap>
            <v-flex 
            xs12
            class="mb-3">
              <h3 class="headline">
              Your Sales
                  <span class="success--text">{{ period }}</span>
              </h3>
            </v-flex>
            <v-flex 
            xs12
            class="mb-3">
              <h3 class="display-5">
              Total Value: SGD {{overall.valueSGD}},
                  <span 
                  v-if="positiveGain"
                  class="success--text">Change: {{ overall.change }}%</span>
                  <span 
                  v-if="!positiveGain"
                  class="error--text">Change: {{ overall.gain }}%</span>
              </h3>
            </v-flex>
            <v-flex xs12 class="text-xs-center"
            v-if="loading">
              <v-progress-circular 
              indeterminate 
              color="success"
              :width="2"
              :size="30"
              ></v-progress-circular>
            </v-flex>
            <v-flex xs 12 
            v-if="!loading">
              <line-chart 
              :data="sales" :labels="labels"></line-chart>
            </v-flex>
          </v-layout>
        </v-card-title>

        <!-- Holdings Datatable -->
        <v-expansion-panel expand focusable>
          <v-expansion-panel-content 
          v-for="(item, i) in 1"
          :key="i">
            <div slot="header">
            View Transactions
            </div>
            <v-card>
              <v-data-table
                :headers="headers"
                :items="orders"
                hide-actions
                class="elevation-1"
              >
                <template slot="items" slot-scope="props">
                  <td>{{ props.item.orderId }}</td>
                  <td class="text-xs-center">{{ props.item.itemName }}</td>
                  <td class="text-xs-center">{{ props.item.quantity }}</td>
                  <td class="text-xs-center">{{ props.item.userName }}</td>
                  <td class="text-xs-center">{{ props.item.destination }}</td>
                  <td class="text-xs-center">{{ props.item.dateOrdered }}</td>
                  <td class="text-xs-center">{{ props.item.dateDelivered }}</td>
                  <td class="text-xs-left">{{ props.item.userCheck }}</td>
                  <td class="text-xs-center">{{ props.item.priceSGD }}</td>
                  <td class="text-xs-center">{{ props.item.amountPaid }}</td>
                  <td class="text-xs-left">{{ props.item.discount }}</td>
                  <td class="text-xs-left">{{ props.item.fee }}</td>
                  <td class="text-xs-left">{{ props.item.netSales }}</td>
                </template>
                <template slot="no-data">
                  <v-alert :value="true" color="warning" icon="warning">
                    No data to display here yet.
                  </v-alert>
                </template>
              </v-data-table>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import LineChart from '@/components/lineChart'

export default {
  components: {
    LineChart
  },
  data: () => ({
    drawer: true,
    period: 'Last Month',
    admins: [
      ['Management', 'people_outline'],
      ['Settings', 'settings']
    ],
    cruds: [
      ['Create', 'add'],
      ['Read', 'insert_drive_file'],
      ['Update', 'update'],
      ['Delete', 'delete']
    ],
    headers: [
      {
        text: 'Order ID',
        value: 'orderId'
      },
      {
        text: 'Item Name',
        value: 'itemName'
      },
      {
        text: 'Quantity',
        value: 'quantity'
      },
      {
        text: 'Client',
        value: 'userName'
      },
      {
        text: 'Destination',
        value: 'destination'
      },
      {
        text: 'Date Ordered',
        value: 'dateOrdered'
      },
      {
        text: 'Date Delivered',
        value: 'dateDelivered'
      },
      {
        text: 'User Verified',
        value: 'userCheck'
      },
      {
        text: 'Retail Price (SGD)',
        value: 'priceSGD' },
      {
        text: 'Amount Collected (SGD)',
        value: 'amountPaid'
      },
      {
        text: 'Discount Applied (%)',
        value: 'discount'
      },
      {
        text: 'Blk.Party Fee (SGD)',
        value: 'fee'
      },
      {
        text: 'Net Sales (SGD)',
        value: 'netSales'
      }
    ]
  }),
  computed: {
    vendor () {
      return this.$store.getters.getVendor
    },
    overall () {
      return this.$store.getters.getOverall
    },
    positiveGain () {
      return this.overall.gain >= 0
    },
    orders () {
      return this.$store.getters.getVendorOrders
    },
    sales () {
      return this.$store.getters.getSales
    },
    labels () {
      return this.$store.getters.getLabels
    },
    loading () {
      return this.$store.getters.getLoading
    }
  }
}
</script>

<style scoped>
  .dashboard {
    position: absolute;
    top: 4%;
    bottom: 4%;
    right: 5%;
    left: 28%;
    overflow: auto;
  }

  @media only screen and (max-width: 400px) {
    .dashboard {
      position: relative;
      top: 0%;
      bottom: 0%;
      right: 0%;
      left: 0%;
      overflow: auto;
    }
  }
</style>




