  // action to get all items
  itemsApi ({commit}) {
    commit('SET_LOADING', true)
    firebase.database().ref('items').child('booze').once('value')
    .then(data => {
      const items = []
      const obj = data.val()
      for (let key in obj) {
        items.push({
          itemId: obj[key].id,
          name: obj[key].name,
          description: obj[key].description,
          priceSGD: obj[key].priceSGD,
          discount: obj[key].discount,
          vendorId: obj[key].vendorId
        })
      }
      commit('SET_ITEMS', items)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to get performance chart data
  chartApi ({commit, state}) {
    commit('SET_LOADING', true)
    const vendorId = state.vendor.id
    firebase.database().ref('vendors').child(vendorId).child('performance').orderByKey().once('value')
    .then(d => {
      const chartData = []
      const chartLabels = []
      const obj = d.val()
      // push data and labels into separate arrays
      for (let key in obj) {
        chartData.push({
          index: key,
          valueSGD: obj[key].valueSGD
        })
        chartLabels.push({
          index: key,
          date: obj[key].date
        })
      }
      // sort data and label arrays by index
      const sales = chartData.reduce(function (d, e) {
        d[e.index] = e.valueSGD
        return d
      }, [])
      const labels = chartLabels.reduce(function (l, e) {
        l[e.index] = e.date
        return l
      }, [])
      commit('SET_SALES', sales)
      commit('SET_LABELS', labels)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // user login actions
  userLogin ({commit}, payload) {
    const newUser = {
      email: payload.email
    }
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      commit('SET_LOADING', false)
      commit('SET_USER', {
        ...newUser,
        id: data.user.uid
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // vendor login actions
  vendorLogin ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const vendorId = data.user.uid
      firebase.database().ref('vendors').child(vendorId).child('profile').once('value')
      .then(profile => {
        const obj = profile.val()
        const newVendor = {
          id: vendorId,
          email: payload.email,
          name: obj.name,
          dateJoined: obj.dateJoined,
          avatar: obj.avatar
        }
        commit('SET_VENDOR', newVendor)
        commit('SET_LOADING', false)
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // autoUserLogin ({commit}, payload) {
  //   commit('SET_USER', {
  //     id: payload.uid,
  //     email: payload.email
  //   })
  // },
  // autoVendorLogin ({commit}, payload) {
  //   commit('SET_VENDOR', {
